.fade-in-main {
  animation: fadeIn ease 5s;
  -webkit-animation: fadeIn ease 5s;
  -moz-animation: fadeIn ease 5s;
  -o-animation: fadeIn ease 5s;
  -ms-animation: fadeIn ease 5s;
}

@keyframes fadeIn {
  0% {
    opacity:0;
  }
  100% {
    opacity:1;
  }
}

@-webkit-keyframes fadeIn {
  0% {
    opacity:0;
  }
  100% {
    opacity:1;
  }
}
.about {
  padding-left: 20rem;
  display: flex;
}

.picture {
  position: relative
}

.picture::after {
  content: '';
  position: absolute;
  width: 100%;
  height: 20rem;
  border: 2px solid#b8e2fc;
  border-radius: 1%;
  left: 1rem;
  top: 10.5rem;
  z-index: -1;
  -webkit-animation: fadeIn ease 10s;
          animation: fadeIn ease 10s;
}

.picture img {
  height: 20rem;
  width: 20rem;
  border-radius: 1%;
  margin-top: 10rem;

}

.about_me {
  margin-top: 20rem;
  margin-left: 0.5rem;
  margin-right: 5rem;
  border: 2px solid#d7eaf5;
  border-radius: 1%;

  padding: 2rem;
  position: relative;
  -webkit-animation: fadeIn ease 2s;
          animation: fadeIn ease 2s;
}

.about_text {
  -webkit-animation-duration: 4s;
          animation-duration: 4s;
  -webkit-animation-name: slideT2B;
          animation-name: slideT2B;
}

.about_text h2 {
  font-family: 'Pontano Sans', sans-serif;
  font-size: 3rem;
  -webkit-text-decoration: underline solid lightblue;
          text-decoration: underline solid lightblue;
}

.about_text h4 {
  font-family: 'Pontano Sans', sans-serif;
}

.about_me::after {
  border-top: 10px solid #f5fcff;
  border-radius: 1%;
  background-color: #f5fcff;
  content: "";
  margin: 0 auto;
  position: absolute;
  top: 1rem;
  left: 1rem;
  z-index: -1;
  width: 101%;
  height: 100%;
  -webkit-animation: fadeIn ease 4s;
          animation: fadeIn ease 4s;
}

.about_me p {
  color: #607a8a;
}
.about_me h2 {
  color: #43494d;
}

.about_me h4 {
  color: #43494d;
}

.about_me ul {
  -webkit-columns: 2;
          columns: 2;
}

.about_me li {
  color: #607a8a;
  position: relative;
  list-style-type: circle;
}

@media (max-width: 860px) {
  .about {
    padding-top: 0;
    flex-direction: column;
    padding-left: 1.5rem;
    padding-right: 1.5rem;
  }

  .picture img {
    margin-top: 8rem;
  }
  .picture::after {
    left: 0.6rem;
    top: 8.5rem;
