    margin-right: 0.5rem;
  }
}

@media (max-width: 360px) {
  .picture img {
    height: 17rem;
    width: 17rem;
  }
  .picture::after {
    left: 0.6rem;
    top: 8.5rem;
    width: 17rem;
    height: 17rem;
  }
}



@-webkit-keyframes slideL2R {
  from {
    margin-left: -100%;
    margin-top: -50%
  }

  to {
    margin-left: 0%;
  }
}



@keyframes slideL2R {
  from {
    margin-left: -100%;
    margin-top: -50%
  }

  to {
    margin-left: 0%;
  }
}

@-webkit-keyframes slideT2B {
  from {
    height: 100%;
    margin-top: -100%
  }

  to {
    height: 100%;
    margin-top: 0rem;
  }
}

@keyframes slideT2B {
  from {
    height: 100%;
    margin-top: -100%
  }

  to {
    height: 100%;
    margin-top: 0rem;
  }
}

