.back {
  position: absolute;
  top: 40%;
  background: none;
  border: none;
  outline: none;
  width: 40px;
  height: 75px;
  line-height: 80px;
  color: white;
  text-align: center;
  margin: 0 1px;
  background: rgba(104, 104, 104, 0.3);
  border-radius: 0 75px 75px 0;
  z-index: 25;
  -webkit-filter: blur(2px);
          filter: blur(2px);  
}

.back:hover {
  color:#71acd9;
  background-color: rgba(0,0,0,0.1); 
  cursor: pointer;
}

.forward {
  position: absolute;
  top: 40%;
  right: 0;
  background: none;
  border: none;
  outline: none;
  width: 40px;
  height: 75px;
  line-height: 80px;
  color: white;
  text-align: center;
  margin: 0 1px;
  background: rgba(104, 104, 104, 0.3);
  border-radius: 75px 0 0 75px;
  z-index: 25;
  -webkit-filter: blur(2px);
          filter: blur(2px);  
}

.forward:hover{
  color:#71acd9;
  background-color: rgba(0,0,0,0.1); 
  cursor: pointer;
}

.overlay {
  position: absolute;
  top: -1%;
  bottom: 0;
  left: -1%;
  right: 0;
  height: 105%;
  width: 105%;
  transition: .5s ease;
  background-color: rgba(255, 255, 255, 0.7);
  z-index: 50;
}

.container {
