.modal {
  transition: 1.1s ease-out;
  box-shadow: 
    1rem 1rem 1.5rem rgba(0, 0, 0, 0.3);
  -webkit-filter: blur(0);
          filter: blur(0);
  -webkit-transform: scale(1);
          transform: scale(1);  
  opacity: 1;
  visibility: visible;
  position: absolute;
  z-index: 200;
  position: fixed; 
  top: 15vh; 
  left: 37vw;
  right: 15vw;
  max-width: 60%;
  margin-right: 3vw;
  max-height: calc(100vh - 210px);
  overflow-y: auto;
}

